/* general */

body {
  font-family: "Nunito", Semi-bold;
  background-color: #ffffff !important;
}

h1 {
  font-size: 2.5em;
}

/* Partie 1 */

.partie1 {
  background-image: url("./fond.png");
  background-size: cover; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
.colUserMobile {
  display: none;
}

.logoPartie1 {
  text-align: center;
  padding-bottom: 11%;
}

.logoPartie1 img {
  width: 25%;
}

.colButton {
  text-align: right;
  margin-top: 0.5%;
}
.btn {
  background-color: transparent !important;
  border-color: white;
  border-radius: 28px;
}

.trigger {
  border-radius: 28px;
  background-color: #f5a624;
  padding: 2%;
  border-width: 0px;
  color: white;
  font-size: 18px;
  padding-left: 4%;
  padding-right: 4%;
  margin-bottom: 19%;
}

/* partie 2 services titres */

.partie2 {
  padding-top: 20%;
  z-index: 2;
}
.enSavoirPlus {
  border-radius: 28px;
  background-color: white;
  color: black !important;
  border-width: 1px;
  border-color: black;
  margin-bottom: 3%;
}

.partie2Titre {
  text-align: center;
  color: #f5a624;
  -webkit-animation: focus-in-expand-fwd 1.2s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: focus-in-expand-fwd 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.partie2SousTitre {
  text-align: center;
  animation: focus-in-expand-fwd 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

/*mockups*/
.mockup1 {
  position: absolute;
  text-align: center;
  top: 29%;
  left: -6%;
  z-index: 1;
}
.mockup2 {
  position: absolute;
  text-align: center;
  top: 40%;
  left: 35%;
  z-index: 3;
}

.mockup1 img {
  width: 20%;
}
.mockup2 img {
  width: 35%;
}
/* partie 3 services blocs */

.partie3 {
  padding-top: 10%;
}

.partie3 p {
  padding-left: 17%;
  padding-right: 17%;
  margin-bottom: 0%;
}
.partie3Titre {
  text-align: center;
  color: #f5a624;
}

.clientBloc {
  text-align: center;

  transition: transform 0.4s;
}

.serveurBloc {
  text-align: center;
  transition: transform 0.4s;
}
.serveurBloc:hover {
  transform: scale(1.2);
  transition: transform 0.8s;
}
.serveurBloc:hover h3 {
  transform: scale(1.2);
  transition: transform 0.8s;
  color: #f5a624;
}

.paraSer {
  font-size: 18px;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.colorA {
  color: black;
}
.restaurateurBloc {
  text-align: center;
  transition: transform 0.4s;
}

.restaurateurBloc:hover {
  transform: scale(1.2);
  transition: transform 0.8s;
}
.restaurateurBloc:hover h3 {
  transform: scale(1.2);
  transition: transform 0.8s;
  color: #f5a624;
}

.clientBloc img {
  width: 12%;
  margin-bottom: 6%;
}

.clientBloc:hover {
  transform: scale(1.2);
  transition: transform 0.8s;
}
.clientBloc:hover h3 {
  transform: scale(1.2);
  transition: transform 0.8s;
  color: #f5a624;
}

.serveurBloc img {
  width: 24%;
  margin-bottom: 7%;
}

.restaurateurBloc img {
  width: 22%;
  margin-bottom: 5%;
}

.video {
  width: 100%;
}

.popup-content {
  left: 0px !important;
}

/* partie 4 environnement*/

.partie4 {
  padding-top: 10%;
}

.partie4Titre {
  text-align: center;
  color: #f5a624;
}

.partie4 img {
  width: 14%;
  margin-top: 10%;
}
.partie4SousTitre {
  text-align: center;
}

/*partie 5 contact */

.partie5 {
  background-color: #f5a624;

  padding-bottom: 3%;
}

.partie5 img {
  width: 26%;
}

.partie5image {
  text-align: center;
  padding-top: 4%;
}

.partie5Contact {
  text-align: center;
  color: #ffffff;
  padding-top: 4%;
}

.adresseContact a {
  text-decoration: underline #c4c4c4;
  color: #c4c4c4 !important;
  font-size: 28px;
}
.treeIMG:hover {
  -webkit-animation: wobble-hor-bottom 0.8s both;
  animation: wobble-hor-bottom 0.8s both;
}

@-webkit-keyframes wobble-hor-bottom {
  0%,
  100% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
  }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
    transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
    transform: translateX(15px) rotate(6deg);
  }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
    transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
    transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
    transform: translateX(-6px) rotate(-1.2deg);
  }
}
@keyframes wobble-hor-bottom {
  0%,
  100% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
  }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
    transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
    transform: translateX(15px) rotate(6deg);
  }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
    transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
    transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
    transform: translateX(-6px) rotate(-1.2deg);
  }
}

a {
  color: white !important;
  font-size: 28px;
}

.fab {
  color: #ffffff;
  margin: 12px;
  font-size: 37px;
  margin-right: 10px;
}

.copy {
  text-align: center;
  font-size: 10px;
}

.copy a {
  font-size: 18px;
}
.iconeFab {
  text-align: center;
}

.mockup1mobile {
  display: none;
}

.mockup1Ipad {
  display: none;
}

.mockup2Ipad {
  display: none;
}

@media (max-width: 640px) {
  /* CSS mobile ici */

  .logoPartie1 {
    padding-bottom: 3% !important;
  }

  .logoPartie1 img {
    margin-top: -9% !important;
    width: 50% !important;
  }

  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  .partie2 h1 {
    font-size: 1.8rem;
  }

  .partie2 {
    padding-top: 6%;
  }

  .partie2 h3 {
    font-size: 1.6rem;
  }

  .mockup2 {
    display: none;
  }

  .logoPartie1 img {
    width: 63%;
    margin-top: 20%;
    padding-bottom: 0%;
  }
  .mockup1mobile {
    display: block !important;
    text-align: center;
  }

  .mockup1mobile img {
    width: 68%;
    padding-right: 10%;
  }

  .colButton {
    display: none;
  }
 
  .clientBloc {
    margin-bottom: 20%;
  }
}

@media (min-width: 576px) and (max-width: 736px) {
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }

  .mockup1Ipad {
    display: block;
    position: absolute;
    top: 13%;

    left: 19%;
  }

  .mockup1Ipad img {
    width: 119%;
  }

  .mockup2Ipad {
    display: block;
    position: absolute;
    top: 15%;
    left: 42%;
  }

  .mockup2Ipad img {
    width: 109%;
  }

  .colButton {
    display: none;
  }
}

/*ipad pro only*/

@media only screen and (min-device-width: 1366px) and (max-device-width: 1366px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 2) {
  .mockup1 img {
    width: 31%;
  }

  .mockup2 {
    top: 40%;
    left: 23%;
    width: 70%;
  }

  .mockup2 img {
    width: 41%;
  }
}

@-webkit-keyframes slide-in-top {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-top {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes focus-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    -webkit-transform: translateZ(-800px);
    transform: translateZ(-800px);
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1;
  }
}
@keyframes focus-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    -webkit-transform: translateZ(-800px);
    transform: translateZ(-800px);
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1;
  }
}
